@import "../../styles/common";
//box-shadow:x  y blur  spread  color  inset;
/*
x:设置水平阴影的位置(X轴),可以使用负值;（必须）
y:设置垂直阴影的位置(y轴),可以使用负值;（必须）
blur:设置阴影模糊半径;(可选)
spread:扩展半径,设置阴影的尺寸;（可选）
color:设置阴影的颜色;（可选）
inset:这个参数默认不设置.默认情况下为外阴影,inset表示内阴影.（可选）
*/

.listItem {
  height: 8rem;
  width: 400px;
  border-color: #EEE;
  border-width: 1px;
  border-style: solid;
  background-color: transparent;
  margin-bottom: 50px;
  border-radius: 10px;
  box-shadow: #EEE -20px 20px 10px 5px;//边框内阴影
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: center;
  flex-shrink: 0;
  padding: 0 10px;
}

.title{
  font-family: $fontFamily;
  font-size: $fontMax;
  margin: 5px 0px;
  font-weight: bold;
}

.content{
  font-size: $fontCommon;
  word-break: break-word;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow : hidden;
  text-overflow: ellipsis;
}
